.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.7)),
    url(./images/background.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
  background-attachment: fixed;

  .txt {
    font-size: 3.5rem;
    color: #fff;
    margin-left: 10rem;
    margin-top: 25rem;
    h1 {
      font-weight: 300;
    }
  }
  em {
    color: #91e5f6;
  }
}
